import React, { Component } from "react"
import { connect } from "react-redux"
import OraModal from "eshop/modules/core/components/ui/Modal"
import { showCustomerSelectedModal, closeCustomerSelectedModal } from "../actions/data"
import { getCustomerSelectedModalIsShow } from "../selectors/root"

class OraFixCustomerSelectedModalComponent extends Component {

    constructor(props){
        super(props)
        this.handleShowModalCustomerSelected=this.handleShowModalCustomerSelected.bind(this);
    }

    handleShowModalCustomerSelected(e) {
        e.preventDefault();
        this.props.openCustomerSelectedModal();
    }

    closeModal() {
        this.props.closeCustomerSelectedModal();
    }

    getMessageText() {
        return ( {__html: this.props.descriptions.messageText})
    }

    render() {
        return <div>
            <OraModal onClose={() => this.closeModal()} open={this.props.customerSelectedModalIsOpen} id={"oraCustomerSelectedModalId"} showClose={false} escapeClose={false} clickClose={false} size="narrow">
                <div className="l-page-row u-font-bold" >
                    <div className="l-row">
                        <div className="l-col l-col-12">
                            <div dangerouslySetInnerHTML={this.getMessageText()}/>
                        </div>
                    </div>
                </div>
            </OraModal>
        </div>
    }
}

const mapStateToProps = state => ({
    customerSelectedModalIsOpen: getCustomerSelectedModalIsShow(state),
})

const mapDispatchToProps = dispatch => ({
    closeCustomerSelectedModal: () => dispatch(closeCustomerSelectedModal()),
    openCustomerSelectedModal: () => dispatch(showCustomerSelectedModal()),
})

export default connect(mapStateToProps, mapDispatchToProps)(OraFixCustomerSelectedModalComponent);

